import React, {
  Component
} from 'react';
import {
  Grid,
  Cell,
  List,
  ListItem,
  ListItemContent
} from 'react-mdl';

class Contact extends Component {
  render() {
    return (
      <div className="contact-body">
        <Grid className="contact-grid" >
          <Cell col={6}>
            <h2> Hongzhi Zhang </h2>
            <img
            alt="my-profile"
            className="contact-image"
            />
            <p style={{
              margin: 'auto',
              paddingTop: '1em'
            }}>
              Currently actively seeking for suitable positions in IT industry, been learning, practicing and developing projects in different programming languages and IDE/Editors, and uploading them on my portfolio site. I am very passionate about what I do and what I seek for - problem solving and new technologies. Contact me if you're interested in my application, I am looking forward to it!
          </p>
          </Cell>
          <Cell col={6}>
            <h2> Contact Me </h2>
            <hr />
            <div className="contact-list">
              <List>
                <ListItem>
                  <ListItemContent style={{ fontSize: '30px', fontFamily: 'Anton' }} >
                    <i className="fa fa-phone-square" aria-hidden="true" />
                    +45 31 87 04 90 </ListItemContent>
                </ListItem>
                <ListItem>
                  <ListItemContent style={{ fontSize: '30px', fontFamily: 'Anton' }} >
                    <i className="fa fa-envelope" aria-hidden="true" />
                    zhhzh2217@gmail.com </ListItemContent> </ListItem >
                <ListItem>
                  <ListItemContent style={{ fontSize: '30px', fontFamily: 'Anton' }}>
                    <i className="fa fa-skype" aria-hidden="true" />
                    naobuzhugu </ListItemContent> </ListItem >
                <ListItem href="https://www.linkedin.com/in/hongzhizhang/">
                  <ListItemContent  style={{ fontSize: '30px', fontFamily: 'Anton' }}>
                    <i className="fa fa-linkedin-square" aria-hidden="true" />
                    <a href="https://www.linkedin.com/in/hongzhizhang/" > https://www.linkedin.com/in/hongzhizhang/ </a>
                     </ListItemContent> </ListItem>
              </List>
            </div>
          </Cell>
        </Grid>
      </div >)
  }
}

export default Contact;
