import React, { Component } from 'react';
import {Grid, Cell} from 'react-mdl';

class Education extends Component{
  render(){
    return(
      <Grid>
      <Cell col={2}>
      <p style={{textAlign:'right', marginRight:'10%', fontWeight:'bold'}}>{this.props.startYear} - {this.props.endYear}</p>
      </Cell>
      <Cell col={10}>
      <h4 style={{marginTop:'0'}}>{this.props.degreeName}</h4>
      <p style={{fontSize:'1.3em' , fontFamily:'bold'}}>{this.props.schoolName}</p>
      <p>{this.props.curriculumDescription}</p>
      </Cell>
      </Grid>
    )
  }
}

export default Education;
