import React, { Component } from 'react';
import {Grid, Cell} from 'react-mdl';

class Experience extends Component{
  render(){
    return(
      <Grid>
      <Cell col={2}>
      <p style={{textAlign:'right', marginRight:'10%', fontWeight:'bold'}}>{this.props.startYear} - {this.props.endYear}</p>
      </Cell>
      <Cell col={10}>
      <h4 style={{marginTop:'0px'}}>{this.props.jobTitle}</h4>
      <p style={{fontSize:'1.3em', fontFamily:'bold'}}>{this.props.companyName}</p>
      <p>{this.props.jobDescription}</p>
      <p>{this.props.jobDescription2}</p>
      </Cell>
      </Grid>
    )
  }
}

export default Experience;
